import React from 'react';
import { Route, Link } from 'react-router-dom';
import Currency from './Currency';
import KeBitcoin from './KeBitcoin';
import KeRupiah from './KeRupiah';


class App extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="container text-center p-4">
                    <Link to="/"><img className="my-3" src="data:image/png;base64," /></Link>
                    <ul class="nav nav-tabs">
                        <li class="nav-item">
                            <Link to="/bitcoin_today" className="btn bg-light">Bitcoin Hari Ini</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/ke_bitcoin" className="btn bg-light">Rupiah Ke Bitcoin</Link>
                        </li>
                        <li class="nav-item">
                            <Link to="/ke_rupiah" className="btn bg-light">Bitcoin Ke Rupiah</Link>
                        </li>
                    </ul>
                </div>

                <div id="route">
                    <Route exact path="/" component={"Home"}></Route>
                    <Route path="/bitcoin_today" component={Currency}></Route>
                    <Route path="/ke_rupiah" component={KeRupiah}></Route>
                    <Route path="/ke_bitcoin" component={KeBitcoin}></Route>
                </div>
            </React.Fragment>
        )
    }
}


export default App;